<template>
  <div class="_paylaterAdvancedOption" :class="'-' + advanced_option.name">
    <PaylaterImage :imageSrc="advanced_option.image_url" imgParentClass="_img"/>
    <div class="_wrapper">
      <Title :titleText="advanced_option.title" />
      <div v-if="advanced_option.descriptions" class="_text">
        <template v-if="'thresholds' == advanced_option.name">
          <Paragraph :paragraphText="advanced_option.descriptions.description" />
          <div class="_inputs">
            <div class="payplugUIInput -icon -iconEuro" :class="thresholdsErrorMin ? '-error' : null">
              <Input type="number"
                     inputType="number"
                     step="1"
                     :name="advanced_option.descriptions.min_amount.name"
                     :inputName="advanced_option.descriptions.min_amount.name"
                     :inputValue="advanced_option.descriptions.min_amount.value"
                     :inputMin="this.advanced_option.descriptions.min_amount.default"
                     :inputMax="this.advanced_option.descriptions.max_amount.default"
                     :inputPlaceholder="advanced_option.descriptions.min_amount.placeholder"
                     @focusout="emitChange"
                     data-e2e-name="oneyThresholdMin" />
            </div>
            <Paragraph class="-inter" :paragraphText="advanced_option.descriptions.inter" />
            <div class="payplugUIInput -icon -iconEuro"  :class="thresholdsErrorMax ? '-error' : null">
              <Input type="number"
                     inputType="number"
                     step="1"
                     :name="advanced_option.descriptions.max_amount.name"
                     :inputName="advanced_option.descriptions.max_amount.name"
                     :inputValue="advanced_option.descriptions.max_amount.value"
                     :inputMin="this.advanced_option.descriptions.min_amount.default"
                     :inputMax="this.advanced_option.descriptions.max_amount.default"
                     :inputPlaceholder="advanced_option.descriptions.max_amount.placeholder"
                     @focusout="emitChange"
                     data-e2e-name="oneyThresholdMax" />
            </div>
          </div>
          <div class="_statement" v-if="thresholdsErrorMin || thresholdsErrorMax">
            <div class="_icon installmentErrorIcon">
              <Icon iconName="error_alert" />
            </div>
            {{ advanced_option.descriptions.error.text }}
          </div>
        </template>
        <template v-if="'thresholds' != advanced_option.name">
          <PaylaterOptionDescription
              v-for="description in advanced_option.descriptions"
              :key="description"
              :description="description" />
        </template>
      </div>
    </div>
    <div class="_switch">
      <Switch v-if="advanced_option.switch" :switchName="advanced_option.name" :switchChecked="advanced_option.checked ? true : null"
              :switchData="advanced_option.name" switchEnabledLabel="On"/>
    </div>
  </div>
</template>

<script>
import PaylaterImage from "@/components/atoms/PaylaterImage";
import Title from "@/components/atoms/AtomTitle";
import Switch from "@/components/atoms/AtomSwitch";
import Paragraph from "@/components/atoms/AtomParagraph";
import Input from "@/components/atoms/AtomInput";
import Anchor from "@/components/atoms/AtomAnchor";
import PaylaterHeader from "@/components/organisms/Paylater/OrganismPaylaterHeader";
import PaylaterOptionDescription from "@/components/organisms/Paylater/OrganismPaylaterOptionDescription";
import {store} from "@/store/store";
import Icon from "@/components/atoms/AtomIcon";

export default {
  name: "OrganismPaylaterOption",
  components:{ Switch, PaylaterHeader, PaylaterImage, Title, PaylaterOptionDescription, Paragraph, Input, Anchor, Icon },
	computed: {
		payment_method_name: {
			get: () => store.translations.payment_methods.payment_methods[0].name
		}
	},
  props:{
    advanced_option:{
      Type: Object,
      Required: false
    }
  },
  data(){
    return {
      store: store,
      thresholdsError: this.thresholdsError,
      thresholdsErrorMin: this.thresholdsErrorMin,
      thresholdsErrorMax: this.thresholdsErrorMax,
      oney_min_amounts_original: this.oney_min_amounts_original,
      switchData: ""
    }
  },
  methods: {
    emitChange() {
		//TODO:: change this for computed variables and redo the validations
      var oney_min_amounts = parseInt(store.data.oney_min_amounts)
      var oney_min_amounts_original = parseInt(this.advanced_option.descriptions.min_amount.default)
      var oney_max_amounts = parseInt(store.data.oney_max_amounts)
      var oney_max_amounts_original = parseInt(this.advanced_option.descriptions.max_amount.default)
      this.thresholdsErrorMin = false
      this.thresholdsErrorMax = false
      if(oney_min_amounts < oney_min_amounts_original || oney_min_amounts < 0) {
        this.thresholdsErrorMin = true
      }
      if(oney_max_amounts > oney_max_amounts_original || oney_max_amounts < 0) {
        this.thresholdsErrorMax = true
      }
      if(oney_min_amounts > oney_max_amounts) {
        this.thresholdsErrorMax = true
        this.thresholdsErrorMin = true
      }
    }
  }
}
</script>

